
.ratingSelect {
  padding: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
  font-weight: 700;
}
.ratingSelect .rating-type {
  padding: 12px 18px;
  position: relative;
}
.ratingSelect .rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.ratingSelect .rating-type .all,
.ratingSelect .rating-type .positive,
.ratingSelect .rating-type .nagative {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  font-size: 12px;
  font-weight: 700px;
  border-radius: 1px;
  color: #4d555d;
}
.ratingSelect .rating-type .all {
  background: rgba(0,160,220,0.2);
}
.ratingSelect .rating-type .positive {
  background: rgba(0,160,220,0.2);
}
.ratingSelect .rating-type .nagative {
  background: rgba(77,85,93,0.2);
}
.ratingSelect .rating-type .active {
  background: #00a0dc;
  color: #fff;
}
.ratingSelect .check {
  padding: 12px 0;
  height: 24px;
  font-size: 0;
  color: #93999f;
  line-height: 24px;
}
.ratingSelect .check .icon-check_circle {
  padding: 4px;
  line-height: 24px;
  font-size: 20px;
}
.ratingSelect .check .text {
  display: inline-block;
  font-size: 12px;
  line-height: 24px;
  vertical-align: top;
}
.ratingSelect .active .icon-check_circle {
  color: #00a0dc;
}
.ratingSelect .active .text {
  color: #07111b;
}


