
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  line-height: 48px;
  z-index: 50;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 48px;
  font-size: 0;
  color: rgba(255,255,255,0.4);
  z-index: 50;
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #141d27;
  padding: 0 12px;
}
.shopcart .content .content-left .logo-wrapper {
  position: relative;
  display: inline-block;
  top: -10px;
  margin-right: 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #141d27;
}
.shopcart .content .content-left .logo-wrapper .logo {
  display: inline-block;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #2b343c;
  border-radius: 50%;
}
.shopcart .content .content-left .logo-wrapper .logo.highLight {
  background: #00a0dc;
}
.shopcart .content .content-left .logo-wrapper .logo.highLight .icon-shopping_cart {
  color: #fff;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
  font-size: 24px;
  line-height: 44px;
  color: #80858a;
}
.shopcart .content .content-left .logo-wrapper .count {
  position: absolute;
  top: 0;
  left: 32px;
  width: 24px;
  height: 16px;
  border-radius: 6px;
  vertical-align: bottom;
  text-align: center;
  background: #f01414;
  font-size: 12px;
  line-height: 16px;
  font-weight: 700;
  color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.shopcart .content .content-left .price,
.shopcart .content .content-left .desc {
  display: inline-block;
  margin: 12px 0;
  height: 24px;
  vertical-align: top;
  line-height: 24px;
}
.shopcart .content .content-left .price {
  padding-right: 12px;
  font-size: 16px;
  font-weight: 700;
  border-right: 1px rgba(255,255,255,0.1) solid;
}
.shopcart .content .content-left .desc {
  padding-left: 12px;
  font-size: 12px;
}
.shopcart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
  padding: 12px 8px;
  box-sizing: border-box;
  text-align: center;
  font-size: 12px;
  line-height: 24px;
  font-weight: 700;
}
.shopcart .content .content-right.not-enough {
  background: #2b343c;
}
.shopcart .content .content-right.enough {
  background: #00643c;
  color: #fff;
}
.shopcart .balls-wrapper .ball-container .ball {
  position: fixed;
  bottom: 20px;
  left: 30px;
  z-index: 50;
}
.shopcart .balls-wrapper .ball-container .ball .inner {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #00a0dc;
}
.shopcart .list-wrapper {
  position: fixed;
  left: 0;
  bottom: 48px;
  width: 100%;
  z-index: -1;
  color: #07111b;
  background: #fff;
}
.shopcart .list-wrapper .list-header {
  height: 40px;
  padding: 0 18px;
  background: #f3f5f7;
  position: relative;
}
.shopcart .list-wrapper .list-header:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.shopcart .list-wrapper .list-header .title {
  display: inline-block;
  position: relative;
  left: 0;
  font-size: 14px;
  line-height: 40px;
}
.shopcart .list-wrapper .list-header .empty {
  position: relative;
  right: 0;
  float: right;
  font-size: 12px;
  line-height: 40px;
  color: #00a0dc;
}
.shopcart .list-wrapper .list-content {
  padding: 0 18px;
  max-height: 217px;
  overflow: hidden;
}
.shopcart .list-wrapper .list-content .list-item {
  position: relative;
  padding: 12px 0;
}
.shopcart .list-wrapper .list-content .list-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.shopcart .list-wrapper .list-content .list-item .name {
  display: inline-block;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
}
.shopcart .list-wrapper .list-content .list-item .price-wraper {
  display: inline-block;
  position: absolute;
  right: 102px;
}
.shopcart .list-wrapper .list-content .list-item .price-wraper .price {
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
  line-height: 24px;
}
.shopcart .list-wrapper .list-content .list-item .cartControl-wrapper {
  position: absolute;
  top: 26px;
  right: 18px;
  font-size: 14px;
  line-height: 24px;
}
.list-enter-active,
.list-leave-active {
  transition: all 0.8s;
}
.list-enter,
.list-leave-active {
  opacity: 0;
  -webkit-transform: translateY(300px);
          transform: translateY(300px);
}
.list-mask {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.6);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}


