
.cartControl {
  font-size: 0;
}
.cartControl .count {
  display: inline-block;
  width: 24px;
  font-size: 10px;
  line-height: 24px;
  color: #93999f;
  text-align: center;
  vertical-align: top;
}
.cartControl .cart-add,
.cartControl .cart-decrease {
  display: inline-block;
  padding: 4px;
  width: 24px;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}
.cartControl .cart-add .icon-add_circle,
.cartControl .cart-decrease .icon-add_circle,
.cartControl .cart-add .icon-remove_circle_outline,
.cartControl .cart-decrease .icon-remove_circle_outline {
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 0;
}
.cartControl .move-enter-active,
.cartControl .move-leave-active {
  transition: all 0.3s;
}
.cartControl .move-enter,
.cartControl .move-leave-active {
  opacity: 0;
  -webkit-transform: translateX(12px);
          transform: translateX(12px);
}


