
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  width: 100%;
  background: #fff;
  color: #07111b;
  z-index: 40;
  overflow: hidden;
}
.food .content .food-header .image-wrapper {
  position: relative;
  width: 100%;
  padding-top: 100%;
}
.food .content .food-header .image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .content .food-header .back {
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 10px;
  font-size: 20px;
  color: #9399a9;
}
.food .content .food-detail {
  position: relative;
  padding: 18px;
}
.food .content .food-detail .title {
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
}
.food .content .food-detail .detail {
  margin-top: 8px;
  font-size: 0;
  color: #93999f;
}
.food .content .food-detail .detail .sell-count {
  font-size: 10px;
  margin-right: 20px;
  line-height: 20px;
}
.food .content .food-detail .detail .rating {
  font-size: 10px;
  line-height: 20px;
}
.food .content .food-detail .price {
  margin-top: 18px;
  font-weight: 700;
  line-height: 24px;
}
.food .content .food-detail .price .nowPrice {
  font-size: 14px;
  color: #f01414;
}
.food .content .food-detail .price .oldPrice {
  font-size: 10px;
  color: #93999f;
  margin-left: 6px;
  text-decoration: line-through;
}
.food .content .food-detail .addToShopcart {
  position: absolute;
  bottom: 18px;
  right: 18px;
  width: 74px;
  height: 24px;
  border-radius: 12px;
  background: #00a0dc;
  font-size: 10px;
  color: #fff;
}
.food .content .food-detail .addToShopcart .text {
  display: inline-block;
  height: 24px;
  width: 74px;
  line-height: 24px;
  text-align: center;
}
.food .content .food-detail .cartControl-wrapper {
  position: absolute;
  bottom: 10px;
  right: 18px;
  font-size: 14px;
  line-height: 24px;
}
.food .content .info {
  padding: 18px;
}
.food .content .info .title {
  font-size: 14px;
}
.food .content .info .text {
  padding: 6px 8px;
  font-size: 12px;
  color: #4d555d;
  line-height: 24px;
}
.food .content .ratingSelect-wrapper .title {
  margin: 18px 0 6px 18px;
  font-size: 14px;
}
.food .content .rating-content {
  padding: 0 18px;
}
.food .content .rating-content .rate-wraper {
  padding: 16px 0;
  position: relative;
}
.food .content .rating-content .rate-wraper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.food .content .rating-content .rate-wraper .rate-info {
  height: 12px;
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
}
.food .content .rating-content .rate-wraper .rate-info .time {
  float: left;
}
.food .content .rating-content .rate-wraper .rate-info .user {
  float: right;
}
.food .content .rating-content .rate-wraper .rate-info .user .name {
  margin-right: 6px;
  vertical-align: top;
}
.food .content .rating-content .rate-wraper .rate-info .user .avatar-wrapper {
  display: inline-block;
  width: 12px;
  height: 12px;
}
.food .content .rating-content .rate-wraper .rate-info .user .avatar-wrapper .avatar {
  border-radius: 50%;
}
.food .content .rating-content .rate-wraper .rate {
  font-size: 12px;
  margin-top: 6px;
}
.food .content .rating-content .rate-wraper .rate .icon {
  display: inline-block;
}
.food .content .rating-content .rate-wraper .rate .icon .icon-thumb_down {
  line-height: 24px;
}
.food .content .rating-content .rate-wraper .rate .icon .icon-thumb_up {
  line-height: 24px;
  color: #00a0dc;
}
.food .content .rating-content .rate-wraper .rate .text {
  font-size: 12px;
  color: #07111b;
  line-height: 16px;
}
.food .content .rating-content .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


